.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.card__image {
  object-fit: contain;
  border-radius: 5px;
  width: 166px;
  height: 134px;
}

.card__title {
  text-align: left;
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__productCode {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__starsAndReviews {
  justify-content: space-between;
